









































import service from '../../service.js';
import mInput from '../../components/m-input.vue';

export default {
	components: {
		mInput
	},
	data() {
		return {
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			CustomBar: this.CustomBar,
			modalName: null,

			tel: '',
			password: '',
			repassword: '',
			code: '',

			radio: 'A',
			disabled: false,
			time: 0,
			btntxt: '获取验证码',
			checkbox: [{
					value: 'A',
					checked: false
				},
				{
					value: 'B',
					checked: true
				},
				{
					value: 'C',
					checked: false
				}
			],

		};
	},
	onLoad() {
		const last_tel = uni.getStorageSync('last_tel');
		if (last_tel) {
			console.log(last_tel);
		}
		uni.request({
			url: 'http://192.168.0.199:8080/agent/login/last-sms-time',
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			method: 'POST',
			dataType: 'json',
			cache: false,
			data: {
				last_tel: last_tel,
				sms_type: 1,
			},
			success: res => {
				let lists = res;
				let data = lists.data
				if (data.isSuccess == 200) {
					this.time = 60 - parseInt(data.result);
					console.log(this.time)
					this.disabled = true;
					this.timer();
				} else {
					this.time = 0;
					this.disabled = false;
				}
			},
			fail: () => {
				uni.showToast({
					icon: 'none',
					title: '网络异常,请稍后重试'
				});
			},
			complete: () => {}
		});
	},
	methods: {
		upper: function(e) {
			console.log(e)
		},
		lower: function(e) {
			console.log(e)
		},
		scroll: function(e) {
			console.log(e)
			this.old.scrollTop = e.detail.scrollTop
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		//找回密码
		commit() {
			var reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
			if (this.tel == '') {
				uni.showToast({
					icon: 'none',
					title: '请输入手机号码'
				});
				return;
			} else if (!reg.test(this.tel)) {
				uni.showToast({
					icon: 'none',
					title: '手机格式不正确'
				});
				return;
			}
			if (this.tel.length < 11) {
				uni.showToast({
					icon: 'none',
					title: '手机号格式不正确'
				});
				return;
			}
			if (this.code == '') {
				uni.showToast({
					icon: 'none',
					title: '请输入验证码'
				});
				return;
			}
			if (this.password.length < 4) {
				uni.showToast({
					icon: 'none',
					title: '密码最短为 4 个字符'
				});
				return;
			}
			if (this.password != this.repassword) {
				uni.showToast({
					icon: 'none',
					title: '两次密码不一致'
				});
				return;
			}
			uni.request({
				url: 'http://192.168.0.199:8080/agent/login/ajax-find-pwd',
				method: 'GET',
				data: {
					tel: this.tel,
					smsCode:this.code,
					pwd:this.repassword,
					sms_type: 1,
				},
				success: res => {
					this.list = res;
					let data = this.list.data;
					let msg = data.result;
					if (data.isSuccess == 200) {
						this.time = 60;
						this.disabled = true;
						uni.showToast({
							icon: 'none',
							title: msg,
						}, 2000);
						let self = this;
						//记录成功发送验证码手机号
						uni.setStorage({
							key: 'last_tel',
							data: this.tel,
							success: function() {
								self.timer();
							}
						});
			
					}
					if (data.isSuccess == 400) {
						uni.showToast({
							icon: 'none',
							title: "请稍候再试",
						}, 2000);
						this.time = 0;
						setTimeout(this.timer, 1000);
						this.disabled = false;
					}
				},
				fail: () => {
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				},
				complete: () => {}
			});
		},
		//验证手机号码部分
		sendcode() {
			var reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
			if (this.tel == '') {
				uni.showToast({
					icon: 'none',
					title: '请输入手机号码'
				});
				return;
			} else if (!reg.test(this.tel)) {
				uni.showToast({
					icon: 'none',
					title: '手机格式不正确'
				});
				return;
			}
			uni.request({
				url: 'http://192.168.0.199:8080/agent/login/ajax-findpwd-code',
				method: 'GET',
				data: {
					tel: this.tel,
					sms_type: 1,
				},
				success: res => {
					this.list = res;
					let data = this.list.data;
					let msg = data.result;
					if (data.isSuccess == 200) {
						this.time = 60;
						this.disabled = true;
						uni.showToast({
							icon: 'none',
							title: msg,
						}, 2000);
						let self = this;
						//记录成功发送验证码手机号
						uni.setStorage({
							key: 'last_tel',
							data: this.tel,
							success: function() {
								self.timer();
							}
						});

					}
					if (data.isSuccess == 400) {
						uni.showToast({
							icon: 'none',
							title: "请稍候再试",
						}, 2000);
						this.time = 0;
						setTimeout(this.timer, 1000);
						this.disabled = false;
					}
				},
				fail: () => {
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				},
				complete: () => {}
			});


		},
		timer() {
			if (this.time > 0) {
				this.time--;
				this.btntxt = this.time + 's后获取';
				setTimeout(this.timer, 1000);
			} else {
				this.time = 0;
				this.btntxt = '获取验证码';
				this.disabled = false;
			}
		},
		navTo() {
			uni.redirectTo({
				url: 'login'
			});
		}
	}
};
